.form-input {
  display: flex;
  gap: 10px;
  color: #774A62;
}

.field-label {
  width: 40%;
  font-size: 20px;
  background-color: #F9F1F0;
  border-radius: 5px;
  padding: 5px 10px;
}

.input-field {
  width: 60%;
  border-radius: 5px;
  border: 1px solid grey;
  padding-left: 5px;
}